#footer {
    width: 100%;
    height: 50px;
    background-color: #5CC5C9;
    position: absolute;
    bottom: 0px;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#footer-text-container {
    display: flex;
    flex-direction: row-reverse;
    padding-right: 100px;
}

.iconation-footer-item {
    height: 30px;
    line-height: 30px;
    padding-right: 10px;
    font-weight: 500;
}